/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
    border: 1px solid;
    border-color: #ccc #ccc #bbb;
    border-radius: 3px;
    background: #e6e6e6;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5), inset 0 15px 17px rgba(255, 255, 255, 0.5), inset 0 -5px 12px rgba(0, 0, 0, 0.05);
    color: rgba(0, 0, 0, .8);
    @include font-size(0.75);
    line-height: 1;
    padding: .6rem 1rem .4rem;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.8);
    &:hover {
        border-color: #ccc #bbb #aaa;
        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.8), inset 0 15px 17px rgba(255, 255, 255, 0.8), inset 0 -5px 12px rgba(0, 0, 0, 0.02);
    }
    &:active,
    &:focus {
        border-color: #aaa #bbb #bbb;
        box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15);
    }
}
/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
    color: #666;
    border: 1px solid #ccc;
    border-radius: 3px;
    &:focus {
        color: #111;
    }
}
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
    padding: 3px;
}
textarea {
    padding-left: 3px;
    width: 100%;
}

/*--------------------------------------------------------------
# Search only
--------------------------------------------------------------*/

/* Buttons */
.main-navigation button{
	border: none;
	background: transparent;
    box-shadow: none;
    color: #248a83;
    font-size: 1rem;
    line-height: 1;
    outline: none;
	padding: 0;
    text-shadow: none;
}

/* Fields */
.main-navigation input[type="search"]{
	color: #666;
	outline: none;
	border-style: none;
	display: none;
	&:focus {
		color: #111;
	}
	&:active {
		border-style: none;
	}
}

.main-navigation input[type="search"] {
	padding: 3px;
	width: 12rem;
}


